/*
** Header
*/

// Site titles

.site-title {
  font-size: rem-calc(32px);
  margin: 0;
}

// Logo
.logo {
  font-family: $logo-font;

  & a {
    color: $nav-font-color-blue;
    @include setTransition();

    &:hover {
      color: darken($nav-font-color-blue, 10);
      @include setTransition();
    }
  }
}
