/*
** Login form
*/

.login-form {

  &--button-holder {
    text-align: right;
  }
}

.signup-form {
  margin-top: 2rem;

  & .row.input-holder {
    margin-bottom: 1rem;
  }

  & .legend {
    font-size: rem-calc(12px);
    color: darken($panel-title-color, 25);
  }

  & [type='text'], & [type='password'], & [type='date'], & [type='datetime'],
  & [type='datetime-local'], & [type='month'], & [type='week'], & [type='email'],
  & [type='number'], & [type='search'], & [type='tel'], & [type='time'], & [type='url'],
  & [type='color'], & textarea {
    margin: 0;
  }
}
