/*
** Place
*/

.place {

  &--details {
    margin-top: 0.6rem;
  }

  // Start header
  &--header {
    position: relative;
    padding: 1rem;
    color: $place-header-color;
    background-color: $place-header-backgound-color;
    font-size: rem-calc(14px);
  }

  &--title {
    font-size: rem-calc(24px);
    margin-bottom: 1rem;
    margin-top: 2rem;

    & .error {
      font-size: rem-calc(14px);
    }

    .edition & .place--input {
      font-size: rem-calc(24px);
    }
  }

  &--labels {
    list-style: none;
    margin: 0;
    margin-left: 1.4rem;

    &--icon {
      float: left;
      position: relative;
      bottom: -4px;
    }
  }

  &--labelitem {
    float: left;
    margin-right: 1rem;
  }
  // End header


  // Start main
  &--main {
    padding: 1rem;
    font-size: rem-calc(14px);

    &--icon {
      margin-right: 0.4rem;
      font-size: rem-calc(16px);
      position: relative;
      bottom: -1px;

      .edition & {
        font-size: 22px;
        bottom: -8px;
      }
    }

    // error
    & .error {
      font-size: rem-calc(12px);
      margin-top: -0.6rem;
      color: $place-error-color;
    }
  }

  &--section-icon {
    display: none;

    @media screen and #{breakpoint(medium)} {
      display: inline-block;
    }
  }

  &--info--holder {
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;

    &:not(:first-child) {
      border-top: 1px solid $place-border-color;
    }
  }
  // End main

  // Start action
  &--action {

    &--wrapper {
      text-align: center;
    }

    &--holder {
      display: inline-block;
      width: auto;
      margin: 0 auto;
    }

    &--icon {
      font-size: rem-calc(16px);
      margin-right: 0.5rem;
    }
  }
  // End action

  // Start description
  &--description {
    margin-left: 1rem;

    &--icon {
      font-size: rem-calc(16px);
      float: left;
      position: relative;
      bottom: -4px;
    }
  }
  // End description

  // Start Edition inputs
  &--input,
  &--input:focus {
    display: inline-block;
    width: calc(100% - 30px);
    margin: 0;
    height: auto;
    padding: 4px 0.5rem;
    border: 0;
    color: $place-header-color;
    border-bottom: 2px solid $place-header-color;
    box-shadow: none;
    background-color: transparent;
  }

  &--main-input {
    height: 2rem;
    padding: 0.4rem;
    border: 1px solid #cacaca;
    margin: 0 0 0.6rem;
    font-family: inherit;
    font-size: 0.8rem;
  }

  &--add--icon {
    padding: 0.4rem;
    margin-top: -0.4rem;
    color: $place-header-color;
    cursor: pointer;
  }
  // End Edition inputs

  // Address
  &--address {
    & .button .fa {
      font-size: 1.2rem;
      margin-right: 1rem;
    }
  }
  // End Address

  // Spacing
  &--spacing {
    margin-bottom: 1rem;

    @media screen and #{breakpoint(medium)} {
      margin-bottom: 0;
    }
  }
}
