
// 35. Custom
// -----------

// Default
$font-color-white: #fefefe;
$color-blue: #2199e8;
$color-orange: #FF9E36;
$color-grey: #e6e6e6;
$color-black: #222;
$color-white: #fefefe;


// Logo
$logo-font: "Pacifico";


// Navigation
$nav-background-color: $color-blue;
$nav-font-color-blue: $color-white;


// Label
$label-font-color-blue: $color-blue;

$label-block-backgound: $color-orange;
$label-editor-backgound: $color-grey;


// Card
$card-font-color-blue: $color-blue;
$card-font-color-black: $color-black;

$card-backgound-color: #F3F3F3;
$card-border-color: darken($card-backgound-color, 5);;


// Place
$place-font-color-blue: $color-blue;
$place-font-color-black: $color-black;

$place-header-backgound-color: $color-blue;
$place-header-backgound-edition-color: $color-grey;
$place-header-color: #fefefe;
$place-header-edition-color: $color-black;
$place-border-color: $color-grey;

$place-input-border-color: $color-blue;

$place-error-color: #E26200;


// Panel
$panel-backgound-color: $color-white;
$panel-border-color: $color-orange;
$panel-border-color-blue: $color-blue;
$panel-border-top-color: $color-grey;
$panel-action-color: $color-blue;

$panel-user-section-background-color: $color-blue;
$panel-user-section-color: $color-white;

$panel-title-color: $color-grey;

$close-panel-color: $color-white;
