/*
** Navigation
*/

.navigation {

  &.top-bar {
    padding: 0 0.5rem;
  }

  &--holder {
    background-color: $nav-background-color;
  }

  .menu &--listitem {
    display: inline-block;
    text-align: right;
  }

  .menu &--link,
  .menu &--user-link {
    padding: 1rem 1rem;
    color: $nav-font-color-blue;
    @include setTransition();

    &:hover {
      background-color: darken($nav-background-color, 5);
      @include setTransition();
    }
  }

  .menu &--icon {
    margin-right: 0.5rem;
    margin-top: -4px
  }

  // Button
  &--button {
    &-holder {
      text-align: right;
    }
  }
}
