/*
** Maps
*/
.map{
  &-canvas {
      height: 250px;
      width: 100%;
      margin-bottom: 1rem;
      margin-top: 1rem;

      @media screen and #{breakpoint(medium)} {
        height: 500px;
      }
  }

  &-place {
      height: 180px;
      width: 100%;
      margin-bottom: 0rem;
      margin-top: 0;

      @media screen and #{breakpoint(medium)} {
        height: 250px;
      }
  }
}
