/*
** Panels
*/
.panel {
  position: fixed;
  overflow-y: auto;
  z-index: 1100;
  height: 100%;
  padding-top: 3rem;
  background-color: $panel-backgound-color;
  @include setTransition();

  & form > .row { margin: 0; }

  &.left {
    width: 90%;
    top: 0;
    left: -90%;
    border-right: 4px solid $panel-border-color-blue;

    @media screen and #{breakpoint(medium)} {
      max-width: 400px;
      left: -400px;
    }

    &.show {
      left: 0;
      @include setTransition();
    }
  }

  &.right {
    width: 90%;
    top: 0;
    right: -90%;
    border-left: 4px solid $panel-border-color;

    @media screen and #{breakpoint(medium)} {
      max-width: 500px;
      right: -500px;
    }

    &.show {
      right: 0;
      @include setTransition();
    }
  }

  & .label {
    &--listitem {
      padding: 0;

      &--label {
        padding: 0.4rem 1rem;
        cursor: pointer;
      }

      & input[type="checkbox"], & input[type="radio"] {
        margin: 0;
      }
    }
  }

  // Menu panel
  &-menu {
    padding-top: 0;
  }

  // Start Section
  &--section {
    position: relative;

    &:not(:first-child) {
      border-top: 1px solid $panel-border-top-color;
    }

    // user section
    &.user-section {
      background-color: $panel-user-section-background-color;
      padding: 2rem 2rem 4rem;

      & .navigation--user-link {
        color: $panel-user-section-color;
      }
    }

    // Label site_description
    &.label-section {
      padding: 2rem 0;
    }

    // Navigation elem
    & .navigation {
      &--link {
        display: block;
        padding: 2rem;
        @include setTransition();

        &:hover {
          background-color: darken($panel-backgound-color, 3);
          @include setTransition();
        }
      }

      &--icon {
        margin-right: 1rem;
      }
    }
    // end navigation elem


    // title
    &--title {
      font-size: rem-calc(16px);
      padding: 0 2rem;
      color: darken($panel-title-color, 8);
    }

  }
  // End section


  // Overlay
  &-overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    height: 100%;
    width: 100%;
    background-color: rgba($color-black, 0.3);

    &.show {
      display: block;
    }
  }
  // end of overlay
}


/*
** Label panel
*/

.label-panel{
  &--list {
    &--holder {
      margin-top: 2rem;
      padding: 0 2rem;
    }
  }
}
